import React, { Component } from 'react';
import { NumberCount } from './about/index';
import {Link} from 'react-router-dom';

class About extends Component {
    render() {
        return (
            <section className="about-me t2 s3 shape-am position-relative" id="about">
                <div className="section-icons">
                   
                </div>
                <div className="container">
                <div className="row position-relative">
                    <div className="col-lg-7 animate-element wow delay5 fadeInDown">
                        <div className="flat-title t2">
                            <h4 className="sub-title mg-b22">Idea/Concept</h4>
                            <h2 className="title-section color-d11 mg-b41">An interactive game that focuses on rhythm mini-games</h2>
                            <div className="description">
                                <p className="color-d13 lt-sp01">
                                
                                Where a character is used on a map of obstacles and platforms. In these, the player must press the correct keys to the rhythm of the music to advance.
                                

                                </p>
                            <p className="color-d13 lt-sp01">
                                
                                    This type of game's genre encourages users to be constantly in interaction with the story and answering questions.
                                    Platform game and Rhythm game
                                    Will help the player understand their rhythm and tempo effectively.
                                </p>
                            </div>
                        </div>
                    </div>
                    <div className="fact">
                        <NumberCount />
                        <div className="btn-contact bg-s2 text-center">

                        <Link to="#" className="color-s2 color-d15">Would you like to try our first development version?</Link>
                        </div>
                    </div>
                </div>
                </div>
                <div className="featured-post animate-element wow delay5 fadeInUp" data-wow-delay="0.5s">
                <img src="images/section/51.png" alt="images" />
                </div>
            </section>
        );
    }
}

export default About;
