import React, { Component } from 'react';
import {Link} from 'react-router-dom';
class CaseStudy extends Component {
    render() {
        return (
            <section className="flat-case-study s3" id="portfolio">
                <div className="container">
                    <div className="d-md-flex">
                        <div className="col-left animate-element wow delay5 fadeInDown" data-wow-delay="0.5s">
                            <div className="image-link t2">
                                <div className="featured-post">
                                    <Link to="#"><img src="images/section/52.png" alt="images" /></Link>
                                </div>
                                <div className="portfolio-template-link text-center">
                                    <Link to="#" className="text-white">Statistics</Link>
                                </div>
                            </div>
                            <div className="image-link t2">
                                <div className="featured-post">
                                    <Link to="#"><img src="images/section/53.png" alt="images" /></Link>
                                </div>
                            </div>
                            
                        </div>
                        <div className="flat-spacer" data-desktop={0} data-mobile={100} data-smobile={100} />
                            <div className="col-right animate-element wow delay5 fadeInUp" data-wow-delay="0.5s">
                                <div className="flat-title t2 mg-b60">
                                    <h4 className="sub-title mg-b13">Statistics</h4>
                                    <h2 className="title-section color-d11 mg-b20">Tableau tools</h2>
                                    <p className="color-d13">
                                    With statistics we can generate different objectives and strategies based on real data and specific objectives by users
                                    </p>
                                </div>
                            <div className="pd-right">
                                <div className="image-link t2">
                                    <div className="featured-post">
                                        <Link to="#"><img src="images/section/54.png" alt="images" /></Link>
                                    </div>
                                </div>
                                <div className="image-link t2">
                                    <div className="featured-post">
                                        <Link to="#"><img src="images/section/55.png" alt="images" /></Link>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default CaseStudy;
