import React, { Component } from 'react';

class NumberCount extends Component {
    constructor(props){
        super(props);
        this.state={
            colleft: [
                {
                    id: 1,
                    item: 'f-item color-s2 counter mg-f-item1',
                    datato: 'Unity, Azure DataBase',
                    text: 'Development tools'
                },
                {
                    id: 2,
                    item: 'f-item color-s3 counter mg-f-item2',
                    datato: 'React.js, WebPack',
                    text: 'Development tools'
                }
            ],
            colright: [
                {
                    id: 1,
                    item: 'f-item color-s4 counter mg-f-item3',
                    datato: 'Vite, Npm, Yarn',
                    text: 'Development tools'
                }
            ]
        }
    }
    render() {
        return (
                <div className="d-md-flex mg-b40">
                    <div className="col-left">
                        {
                            this.state.colleft.map(data => (
                                <div className={data.item} key={data.text}>
                                    <div className="counter-box bg-s2 d-flex portfolio-template-link text-center justify-content-center align-items-center">
                                        <div className="inner">
                                            <h3>{data.datato}</h3>
                                            <p>{data.text}</p>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>  
                    <div className="col-right">
                        {
                            this.state.colright.map(data => (
                                <div className={data.item} key={data.id}>
                                    <div className="counter-box bg-s2 d-flex justify-content-center align-items-center">
                                        <div className="inner">
                                            <h3>{data.datato}</h3>
                                            <p>{data.text}</p>
                                        </div>
                                    </div>
                                </div>
                            ))
                        }
                    </div>
                </div>
        );
    }
}

export default NumberCount;
