import React, { Component } from 'react';
import Unity, {UnityContext} from "react-unity-webgl";

const unityContext = new UnityContext({

    loaderUrl: "Build/game.loader.js",
    dataUrl: "Build/game.data",
    frameworkUrl:"Build/game.framework.js",
    codeUrl: "Build/game.wasm", 
  
  });
  


class game extends Component {
    
    render() {
        return (              
            <section className="experienced s3" id="game">
                <div className="container">
                    <div className="background-inner bg-s2">
                        <div className="flat-title animate-element wow delay5 fadeInDown" data-wow-delay="2s">
                        <h1 className="title-section color-d11 mg-b44 text-center">Percussion Delusion</h1>
                        </div>
                        <div className="row animate-element wow delay5 fadeInUp">
                            {
                               
                               <Unity
                               style={{
                                 width:"100%",
                                 justifyContent:"center",
                                 alignSelf:"center",
                               }}
                             unityContext={unityContext}
                       
                             /> 
                            }                       
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default game;
