import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import menus from '../menus';

class Header extends Component {
    constructor(props){
        super(props);
        this.state = {}
    }
    render() {
        return (
            <header id="header" className="header header-style4 has-menu-model">
                <div className="container">
                    <div className="flex-header d-flex justify-content-between align-items-center">
                        <div id="logok" className="logxo">
                            <Link to="/" onClick={() => {window.location.href="/"}} ></Link>
                            <img src="images/logo/itesm.svg" alt=""width="70"height="341"></img>

                        </div>
                        <div dir="rtl" className="btn-menu mobile-header__menu-button">
                            <div className="line line-1" />
                            <div className="line line-2" />
                            <div className="line line-3" />
                            <div className="line line-4" />
                        </div>
                        <div className="content-menu d-lg-flex">
                            <div className="nav-wrap">
                                <nav id="mainnav" className="mainnav">
                                    <ul className="menu ace-responsive-menu" data-menu-style="horizontal">
                                        {
                                            menus.map(menu => (
                                                <li key={menu.id}>
                                                    <Link to={menu.tomenu} 
                                                    onClick={() => {window.location.href=menu.tomenu}}
                                                    className="click-model">{menu.namemenu}</Link></li>
                                            ))
                                        }                           
                                        
                                    </ul>
                                </nav>
                            </div>
                        </div>
                        <div id="logok" className="logxo">
                            <Link to="/" onClick={() => {window.location.href="/"}} ></Link>
                            <img src="images/logo/unnamed.jpg" alt=""width="175"height="50"></img>

                        </div>
                    </div>
                </div>
            </header>
        );
    }
}

export default Header;
