import React, { Component } from 'react';
class Banner extends Component {
    render() {
        return (
            <section className="banner-section s4 two-home" id="home">
                <div className="container">
                    <div className="content-text position-relative">
                        <div className="animate-element wow delay5 fadeInDown" data-wow-delay="0.3s">
                            <div className="tag color-d1 lt-sp53 mg-b34">Software Building and Decision Making - Software Engineering</div>
                            <h1 className="cd-headline clip is-full-width title color-d11 mg-b41">
                                <span>Percussion </span>
                                <span className="cd-words-wrapper color-d6">
                                <b className="is-visible">Delusion</b>
                                <b>Delusion</b>
                               
                                </span>
                            </h1>
                            <p className="color-d13 lt-sp02 mg-b51">
                            Hello, we are a group of students from the Monterrey Institute of Technology and <br />Higher Education committed to generating specific solutions from
                            development <br /> tools, we thank the association Percussive Arts Society for the time provided</p>
                        </div>
                        <div className="animate-element wow delay5 fadeInUp" data-wow-delay="0.3s">
                            <div className="fl-btn btn-general color-s2 btn-hv-border">

                            <form action="https://youtu.be/DhJkjjxu9JU">
                            <button type="submit">Project overview</button>
                            </form>
                            </div>
                        </div>
                    </div>
                    
                </div>
              
            </section>
        );
    }
}

export default Banner;
